import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { retry, catchError } from 'rxjs/operators';
import { Observable, throwError, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class Auth {

  constructor(private http: HttpClient) {
    this.email.next('');
   }

  public email = new Subject();

  setUserEmail(email: string) {
    console.log('SET_USER_EMAIL',email);
    this.email.next(email);
  }

  SignInGoogle(params):Observable<any> {
    let httpParams = {
      profile:params
    };
    const config = { headers: new HttpHeaders().set('Content-Type', 'application/json') };
    console.log('SIGNINGOOGLE');
    return this.http.post('/auth/google', JSON.stringify(httpParams), config).pipe(
      catchError(this.errorHandler)
    )
  }

    login(params):Observable<any> {
      const config = { headers: new HttpHeaders().set('Content-Type', 'application/json') };
      console.log('LOGIIN');
      return this.http.post('/auth/login', JSON.stringify(params), config).pipe(
        catchError(this.errorHandler)
      )
    }

    Signup(params):Observable<any> {
      const config = { headers: new HttpHeaders().set('Content-Type', 'application/json') };
    console.log('SIGNUP');
    return this.http.post('/auth/signup', JSON.stringify(params), config).pipe(
      catchError(this.errorHandler)
    )
  }

  errorHandler(error) {
    let errorMessage = '';
    if(error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);
    return throwError(errorMessage);
 }
}
