import { Component, OnInit } from '@angular/core';
import {RoutingState} from '../services/RoutingHistory/routingState.service';
import {HomeService} from '../services/HomeService/home.service';

@Component({
  selector: 'app-my-shows',
  templateUrl: './my-shows.component.html',
  styleUrls: ['./my-shows.component.scss']
})
export class MyShowsComponent implements OnInit {

  user:string;
  shows: any;

  constructor(routingState: RoutingState,private homeService: HomeService) {
    this.user = routingState.getUser();
  }

  ngOnInit() {
    this.homeService.getUsersShows({user: this.user}).subscribe(shows => {
      this.shows = shows.body;
      console.log('USERS_SHOWS',this.shows);
    });
  }

}
