import { Observable, throwError } from 'rxjs';
import {Config} from '../../models/Config';

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { retry, catchError, filter } from 'rxjs/operators';
import { Router,NavigationEnd, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, RouterState, ActivatedRoute } from '@angular/router';
import {Auth} from '../AuthService/auth.service';


@Injectable({
  providedIn: 'root'
})

export class RoutingState {
  private history = [];
  private user:string;

  constructor(private router: Router,private authService: Auth) {

    this.authService.email.subscribe((email:string)=>{
      this.user = email;
    });
  }

  public loadRouting(): void {
    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(({urlAfterRedirects}: NavigationEnd) => {
        this.history = [...this.history, urlAfterRedirects];
      });
  }

  public getHistory(): string[] {
    return this.history;
  }

  public getUser():string {
    return this.user;
  }

  public getPreviousUrl(): string {
    return this.history[this.history.length - 2] || '/';
  }
}
