import { BrowserModule } from '@angular/platform-browser';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { CookieService, CookieBackendService } from 'ngx-cookie';
import { SocialLoginModule, AuthServiceConfig, GoogleLoginProvider } from 'angularx-social-login';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HomeModule } from './home/home.module';
import { AddModule } from './add/add.module';
import { DetailModule } from './detail/detail.module';
import { LoginModule } from './login/login.module';
import { SignupModule } from './signup/signup.module';
import { RouterModule} from '@angular/router';
import { AboutComponent } from './about/about.component';
import { ErrorComponent } from './error/error.component';
import { MyShowsComponent } from './my-shows/my-shows.component';

const google_oauth_client_id:string = '261560799158-eeeftlbv3utboenfukvfve10iqdscl32.apps.googleusercontent.com';
let config = new AuthServiceConfig([
  {
    id: GoogleLoginProvider.PROVIDER_ID,
    provider: new GoogleLoginProvider(google_oauth_client_id)
  }
]);

@NgModule({
  declarations: [
    AppComponent,
    AboutComponent,
    ErrorComponent,
    MyShowsComponent,
  ],
  imports: [
    NgbModule,
    CommonModule,
    HomeModule,
    AddModule,
    DetailModule,
    LoginModule,
    SignupModule,
    BrowserModule.withServerTransition({ appId: 'serverApp' }),
    RouterModule.forRoot([]),
    AppRoutingModule,
    SocialLoginModule.initialize(config)
  ],
  providers: [{ provide: CookieService, useClass: CookieBackendService }],//
  bootstrap: [AppComponent]
})
export class AppModule { }
