import { Component, OnInit } from '@angular/core';
import { AuthService, GoogleLoginProvider } from 'angularx-social-login';
import {Auth} from './services/AuthService/auth.service';
import {uniqueId} from 'lodash';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, RouterState, ActivatedRoute } from '@angular/router';
import {RoutingState} from './services/RoutingHistory/routingState.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss', "../../node_modules/bootstrap/dist/css/bootstrap.min.css"],
})
export class AppComponent implements OnInit {
  user: string = null;
  title: string = 'Favourite Tracker';

  constructor(private authService: Auth,routingState: RoutingState) {
    routingState.loadRouting();
    this.user = routingState.getUser();
  }

  logout(){
    this.authService.setUserEmail(null);
  }

  ngOnInit() {
    this.authService.email.subscribe((email:string)=>{
      this.user = email;
    });
  }
}


