import { Component, OnInit,Renderer2 } from '@angular/core';
import { FormGroup, FormControl, Validators} from '@angular/forms';
import {HomeService} from '../services/HomeService/home.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['../app.component.scss',"../../../node_modules/bootstrap/dist/css/bootstrap.min.css"],
  styles: [`input.ng-touched.ng-invalid {border:solid red 2px;}
  input.ng-touched.ng-valid {border:solid green 2px;}`]
})

export class AddComponent implements OnInit {
  myForm : FormGroup;
  showName='';
  constructor(private homeService: HomeService, private router: ActivatedRoute,private renderer: Renderer2) {
    console.log('ROUTER ADD',this.router);
    this.myForm = new FormGroup({

      "showName": new FormControl("Show", [Validators.required, this.userNameValidator])
  });
   }

   userNameValidator(control: FormControl): {[s:string]:boolean}{

    if(control.value==="нет"){
        return {"showName": true};
    }
    return null;
}

addShow(){
  this.homeService.postShows(this.showName).subscribe(show=>{
    console.log(show);
  });
  console.log(this.myForm);
}

  ngOnInit() {
    // this.renderer.setStyle(document.body, 'background-color', 'yellow');
    // console.log('ROUTER ADD',this.router);
  }

}
