import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators} from '@angular/forms';
import {Auth} from '../services/AuthService/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService, GoogleLoginProvider } from 'angularx-social-login';
import {uniqueId} from 'lodash';
import {RoutingState} from '../services/RoutingHistory/routingState.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['../app.component.scss',"../../../node_modules/bootstrap/dist/css/bootstrap.min.css"]
})
export class LoginComponent implements OnInit {
  loginForm : FormGroup;
  password:string;
  email:string;
  returnUrl:string;
  user:any = null;
  previousRoute: string;

  constructor(private authService: Auth,private _socioAuthServ: AuthService,private router: Router, private route: ActivatedRoute,private routingState: RoutingState) {
    this.loginForm = new FormGroup({

      "password": new FormControl("password"),
      "email": new FormControl("email"),
  });
  }

  singInGoogle(platform : string): void {
    platform = GoogleLoginProvider.PROVIDER_ID;
    this._socioAuthServ.signIn(platform).then(
      (response) => {
        console.log(platform + " logged in user data is= " , response);
        this.user = response;
        this.email = response.email;
        this.authService.SignInGoogle({id:uniqueId(),email:this.user.email})
        this.authService.setUserEmail(response.email);
        this.router.navigate([`${this.previousRoute}`]);
      }
    )
  }

  // Method to log out.
  signOutGoogle(): void {
    this._socioAuthServ.signOut();
    this.user = null;
    this.authService.setUserEmail(null);
    console.log('User signed out.');
  }

  login(){
    this.authService.login({
      email:this.email,
      password:this.password
    }).subscribe((user)=>{
      console.log('user');
      this.authService.setUserEmail(this.email);
      this.router.navigate([`${this.previousRoute}`]);
      console.log('ROUTER',this.returnUrl);
    });
  }

  logout(){
    this.authService.setUserEmail(null);
  }

  ngOnInit() {
    this.authService.email.subscribe((email:string)=>{
      this.email = email;
      this.returnUrl = '/Home';
      console.log(this.route);
      console.log(this.router);
      this.previousRoute = this.routingState.getPreviousUrl();
      console.log('PREVIOUSE',this.routingState.getPreviousUrl());
    });
  }

}
