import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators} from '@angular/forms';
import {Auth} from '../services/AuthService/auth.service';
import { Router } from '@angular/router';
import {RoutingState} from '../services/RoutingHistory/routingState.service';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['../app.component.scss',"../../../node_modules/bootstrap/dist/css/bootstrap.min.css"],
  styles: [`input.ng-touched.ng-invalid {border:solid red 2px;}
  input.ng-touched.ng-valid {border:solid green 2px;}`]
})
export class SignupComponent implements OnInit {

  signupForm : FormGroup;
  passwordText='';
  emailText='';
  confirmPassword='';
  previousRoute: string;

  constructor(private authService: Auth, private router: Router,private routingState: RoutingState) {
    this.signupForm = new FormGroup({

      passwordText: new FormControl("signupForm"),
      confirmPassword: new FormControl("signupForm"),
      emailText: new FormControl("signupForm"),
  });
  }

  signup(){
    this.authService.Signup({
      email:this.emailText,
      password:this.passwordText
    }).subscribe(show=>{
      console.log(show);
      this.authService.setUserEmail(this.emailText);
    });
    this.router.navigate([`${this.previousRoute}`]);
  }

    // Method to sign in with google.

  userNameValidator(control: FormControl): {[s:string]:boolean}{

    if(control.value==="нет"){
        return {"showName": true};
    }
    return null;
  }

  // signup(){
  //   this.homeService.postShows(this.showName).subscribe(show=>{
  //     console.log(show);
  //   });
  //   console.log(this.myForm);
  // }

  ngOnInit() {
    // this.authService.email.subscribe((email:string)=>{
    //   this.emailText = email;
    // })
    this.previousRoute = this.routingState.getPreviousUrl();
  }

}
