
// import { HttpClient,HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import {Config} from '../../models/Config';

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { retry, catchError } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})

export class HomeService {

  constructor(private http: HttpClient) { }

  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }

  // Get all posts from the API
  getAllPosts(param):Observable<any> {
    return this.http.get('/api/shows', {
      params: param,
      observe: 'response'
    }).pipe(
      retry(1),
      catchError(this.errorHandler)
    )
  }

    getCertainShow(id):Observable<any> {
      return this.http.get(`/api/shows/${id}`, {observe: 'response'}).pipe(
        retry(1),
        catchError(this.errorHandler)
      )}

    errorHandler(error) {
      let errorMessage = '';
      if(error.error instanceof ErrorEvent) {
        // Get client-side error
        errorMessage = error.error.message;
      } else {
        // Get server-side error
        errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
      }
      return throwError(errorMessage);
   }

   postShows(show):Observable<any> {
    let httpParams = {
      showName:show
    };
    const config = { headers: new HttpHeaders().set('Content-Type', 'application/json') };
    return this.http.post('/api/shows', JSON.stringify(httpParams), config).pipe(
      catchError(this.errorHandler)
    )}

    subscribed(showId,user):Observable<any> {
      let httpParams = {
        showId: showId,
        user: user
      };
      const config = { headers: new HttpHeaders().set('Content-Type', 'application/json') };
      return this.http.post('/api/subscribe', JSON.stringify(httpParams), config).pipe(
        catchError(this.errorHandler)
      )
    }

    unsubscribed(showId,user):Observable<any> {
      let httpParams = {
        showId: showId,
        user: user
      };
      const config = { headers: new HttpHeaders().set('Content-Type', 'application/json') };
      return this.http.post('/api/unsubscribe', JSON.stringify(httpParams), config).pipe(
        catchError(this.errorHandler)
      )
    }

    getUsersShows(userEmail):Observable<any> {
      return this.http.get('/api/userShows', {
        params: userEmail,
        observe: 'response'
      }).pipe(
        retry(1),
        catchError(this.errorHandler)
      )
    }
}


